.flash-cards {
  font-size: 24px;
  line-height: 72px;
  width: 216px;
  border: 1px solid black;
  margin: 18px;
  color: whitesmoke;
  cursor: pointer;
  user-select: none;
}

.flash-card.front {
  background-color: cornflowerblue;
}

.flash-card.back {
  background-color: darkolivegreen;
}
